.modalWr {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
}

.modalContent {
  padding: 2rem;
  border-radius: 16px;
}

.close {
  position: relative;
  left: 100%;
  &:hover {
    color: white;
  }
}
